* {
  box-sizing: border-box;
}

.App {
  height: 100%;
  width: 100vw;
  display: grid;
  grid-template-areas: "Navbar SearchBar SearchBar SearchBar"
                       "Navbar Main Main Main"
                       "Player Player Player Player";
  grid-template-columns: 0.5fr repeat(3, 1fr);
  grid-template-rows: 50px 1fr 102px;
  background-color: rgb(0, 5, 56);
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  color: #fff;
}

.Main {
  grid-area: Main;
}


.SearchBar {
  grid-area: SearchBar;
  display: flex;
  align-items: center;
  height: 50px;
  background-color: rgba(255, 255, 255, 0);
}

.SearchBar input {
  width: 300px;
  padding: .30rem 0;
  border: 1px solid #fff;
  border-radius: 20px;
  color: #010c3f;
  text-align: center;
  font-size: 1rem;
  margin-left: 33%;
}

.SearchBar .Search_Button {
  position: relative;
  right: 27px;
  color: rgb(0, 8, 33);
}

.Navbar-wrapper {
  grid-area: Navbar;
  height: 100%;
  background-color: rgb(0, 8, 33);
}

.Navbar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 400px;
  margin: 9rem 0 auto 1rem;
}

a.LinkButton  {
  text-decoration: none;
  text-align: left;
  color: white;
}

a.active.LinkButton {
  color: rgb(247, 9, 9);
}

.Main {
  grid-area: Main;
  height: 100%;
  background-color: rgb(0, 5, 56);
}

.SearchResults {
  width: 100%;
  max-height: 705px;
  padding: 0 4rem;
  overflow-y: scroll;
  z-index: 999;
}

.Tracklist {
  display: flex;
  width: 100%;
}

.Tracklist-tracks {
  width: 50%;
}

.Tracklist-albums {
  width: 50%;
}

.Button {
  float: right;
  padding: .40rem !important;
}


.TrackList {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
  margin: 50px auto;  
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

.Track {
  display: flex;
  max-width: 50%;
  cursor: pointer;
  margin: 0.5rem 0.2rem;
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
}

.Track:hover {
  box-shadow: 0px 5px 8px 2px rgb(0, 0, 0.7);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.Track-action {  
  margin: 0 1.25rem;
  border: 0;
  color: white;
  transition: color .25s;
}

.Track-action:hover {
  color: rgba(265, 265, 265, .5);
}

.Track-information img {
  height: 64px;
  max-width: 64px;
  margin: 0.5rem;
}

.Track-information {
  display: flex;
}

.Track-information button {
  font-size: .6rem;
  font-weight: 300;
  color: rgba(256, 256, 256, 0.8);
}

.Track span{
  margin: auto 1.25rem;
}

.Text {
  margin: auto 0;
}

.Text p {
  font-size: 0.67rem;
  margin: 8px 0;  
  white-space: nowrap;
  width: 18ch;
  overflow: hidden;
  text-overflow: ellipsis;
}

p:last-child {
  font-weight: 100;
}

.PlaylistDisplay {
  border-bottom: 1px solid grey;
}

.Grid {
  display: grid;
  grid: repeat(auto-fill, 150px) / repeat(auto-fill, minmax(125px, 1fr));
  grid-gap: 40px;
  justify-items: center;
  margin: 50px auto 50px auto;  
}

.Show-more {
  display: inline-block;
  margin: auto 0 0 0;
}
.Card {
  width: 150px;
  height: 150px;
  background-color: rgba(0, 0, 20, 0.7);
  box-shadow: 0px 0px 20px 6px rgba(0, 0, 0, 0.7);
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
  text-align: center;
}

.Card:hover {
  box-shadow: 0px 0px 25px 8px rgb(0, 0, 0.7);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.Card img {
  height: 150px;
  max-width: 100%;
  background-color: black;
}

button.Button {
  cursor: pointer;
  padding: .40rem 2rem;
  height: 1.7rem;
  border: 0;
  color: white;
  border-radius: 20px;
  background-color: #000766;
  font-size: .833rem;
  box-shadow: 0 4px 2px 2px #000000;
  transition: background-color .25s;
}

button.Button:hover {
  background-color: rgba(108, 65, 233, .7);
  box-shadow: 0 2px 1px 1px #000000;
}

.Playlist {
  align-items: center;
  overflow-y: scroll;
  width: 90%;
  max-height: 690px;
  margin: 1rem auto;
  background-color: rgb(0, 5, 56);
  font-size: 16px;
  font-weight: 100;
}

.Playlist input {
  width: 100%;
  border: 0;
  outline: 0;
  background: transparent;
  border-bottom: 1px solid #6f6f6f;
  font-family: 'Poppins', sans-serif;
  font-size: 1.5rem;
  color: #fff;
}

.Playlist-save {
  cursor: pointer;
  color: white;
  width: 8rem;
  padding: .5rem 0;
  border: 0;
  border-radius: 54px;
  margin-top: 1.27rem;
  background-color: #6c41ec;
  text-align: center;
  font-size: .7rem;
  transition: background-color .25s;
}

.Playlist-save:hover {
  background-color: rgba(108, 65, 233, .7);
  padding: .5rem 0;
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}



.Player {
  grid-area: Player;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(3, 0, 51);
  height: 100px;
  width: 100%;
  padding: 0 2rem;
}

.track-info {
  width: 33.33%;
  text-align: center;
}

.Player .Control {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 33.33%;
  height: 100%;
}

.back,
.forward {
  width: 40px;
  transition: 300ms;
}

.play-pause,
.play-pause:focus {
  border: 1px solid grey;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  transition: 300ms;
}
.play-pause:hover {
  border: 1px solid white;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}


.back:hover,
.forward:hover {
  width: 40px;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}


.Player .Player-buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  width: 35%;
  margin-top: 10px;
}

.volume {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 33.33%;
}

.volume-bar {
  width: 5rem;
  margin: 0 0.22rem;
}

.Player .Track:hover {
  box-shadow: none;
  -webkit-transform: none;
          transform: none;
}

.Bar-container {
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  
}

.Bar {
  height: 3px;
  background-color: yellow;
  width: 100%;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-radius: 10px;
  transition: 0.4s all ease-out;
}

input[type=range]::-webkit-slider-thumb {
  height: 15px;
  width: 10px;
  border-radius: 5px;
  background: #FFFFFF;
  cursor: pointer;
  -webkit-appearance: none;
  transition: all ease-in-out 0.05s;
} 







